
































































































import { Component, Vue } from 'vue-property-decorator'
import ActionButton from '@/components/ActionButton.vue'
import InputTextField from '@/components/InputTextField.vue'
import InputNumberField from '@/components/InputNumberField.vue'
import ToggleSwitch from '@/components/ToggleSwitch.vue'
import FooterButtons from '@/components/FooterButtons.vue'
import { ConsumeStatus, Status } from '@/@types/component-interfaces/status'
import { namespace } from 'vuex-class'

const Statuses = namespace('Statuses')
const Auth = namespace('Auth')

type SymptomItem = {
  name: string
  label: string
}
@Component({
  components: {
    ActionButton,
    InputTextField,
    InputNumberField,
    ToggleSwitch,
    FooterButtons,
  },
  metaInfo: {
    title: '体調記録',
  },
})
export default class Record extends Vue {
  @Auth.Action
  private signOut!: () => void
  private loading = false
  private message = ''
  symptomItems: SymptomItem[] = [
    {
      name: 'cough',
      label: 'せき',
    },
    {
      name: 'phlegm',
      label: 'たん',
    },
    {
      name: 'suffocation',
      label: '息苦しさ',
    },
    {
      name: 'headache',
      label: '頭痛',
    },
    {
      name: 'sore_throat',
      label: 'のどの痛み',
    },
  ]
  inputSpO2 = ''
  inputPulse = ''
  inputTemperature = ''

  get status(): ConsumeStatus {
    return {
      SpO2: +this.inputSpO2,
      body_temperature: +this.inputTemperature,
      pulse: +this.inputPulse,
      symptom: {
        cough: false,
        phlegm: false,
        suffocation: false,
        headache: false,
        sore_throat: false,
        remarks: '',
      },
    }
  }

  get isSubmittable(): boolean {
    return (
      this.status.body_temperature > 0 &&
      this.status.SpO2 > 0 &&
      Number.isInteger(this.status.SpO2) &&
      this.status.pulse > 0 &&
      Number.isInteger(this.status.pulse)
    )
  }

  get btnTheme(): string {
    return this.isSubmittable ? 'primary' : 'disable'
  }

  itemSelectControl(checked: boolean, value: string): void {
    this.status.symptom[
      value as 'cough' | 'phlegm' | 'suffocation' | 'headache' | 'sore_throat'
    ] = checked
  }

  @Statuses.Action
  private create!: (status: ConsumeStatus) => Promise<Status>

  submitRecord(): void {
    if (this.isSubmittable) {
      this.loading = true
      this.create(this.status).then(
        (data) => {
          this.$router.push({
            name: 'Detail',
            params: {
              statusId: data.statusId,
              message: '体調記録を登録しました',
            },
          })
        },
        (error) => {
          this.loading = false
          this.message = error
        },
      )
    }
  }

  logout(): void {
    this.signOut()
    this.$router.push('/login')
  }
}
