
































import Vue from 'vue'
import ActionButton from '@/components/ActionButton.vue'

export default Vue.extend({
  components: {
    ActionButton,
  },
  props: {
    isOpen: {
      type: Boolean,
      default: false,
    },
  },
  methods: {
    handlePositiveButtonClick() {
      this.$emit('click')
      this.$emit('click-positive')
    },
    handleNegativeButtonClick() {
      this.$emit('click')
      this.$emit('click-negative')
    },
  },
})
